body {
    background-image: linear-gradient(91.9deg, rgba(75, 207, 250, 1) 6.3%, rgba(25, 159, 249, 1) 98.9%);
}

.blur {
    filter: blur(4px);
    -webkit-filter: blur(4px);
    -moz-filter: blur(4px);
    -o-filter: blur(4px);
    -ms-filter: blur(4px);

    animation: blurKeyframe 5s ease-out 0s 1;
    -webkit-animation: blurKeyframe 5s ease-out 0s 1;
    -moz-animation: blurKeyframe 5s ease-out 0s 1;
}

@keyframes blurKeyframe {

    from {
        -webkit-filter: blur(17px);
        -moz-filter: blur(17px);
        -o-filter: blur(17px);
        -ms-filter: blur(17px);
    }

    to {
        -webkit-filter: blur(4px);
        -moz-filter: blur(4px);
        -o-filter: blur(4px);
        -ms-filter: blur(4px);
    }

}

.disabledPointers {
    pointer-events: none;
}

.oswald {
    font-family: 'Oswald', sans-serif;
    font-size: 50px;
}

.img_famous {
    max-height: 300px;
    width: auto;
    border-radius: 5px;
}

.img_incognito {
    width: 300px;
}

.green-letter {
    color: rgb(0, 182, 0);
    border: 2px solid rgb(0, 182, 0);
}